.Button
    color: white
    transition: background-color 0.3s
    z-index: 10

    &:hover
        cursor: pointer

.LinkButton
    width: 100px
    margin-top: 20px
    padding: 10px
    border: none
    background-color: #007bff
    color: white
    font-weight: bold
    border-radius: 5px
    cursor: pointer
    transition: background-color 0.3s
    text-decoration: none
    text-align: center

    &:hover
        background-color: #0056b3
